header {
  position: relative;
  display: inline-block;
  background: var(--bs-body-bg);
  backdrop-filter: blur(0);
  width: 100%;
  z-index: 999;
  transition: all 0.3s ease-out 0s;

  .toggle-rounded-btn{
    height: 30px;
    width: 30px;
    padding: 0;
    line-height: 30px;
    text-align: center;
    font-size: 20px;
  }

  .navbar-brand {
    margin: 0;
    display: inline-block;

    img {
      &.logo {
        height: 2.0625rem;
        max-height: 2.0625rem;
      }
    }

    svg {
      width: 10rem;
    }
  }

  .mobile-offcanvas{
    .navbar-brand{
      padding: 0;
    }
  }

  &.sticky {
    background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    backdrop-filter: blur(0.625em);
    -webkit-animation: header-slide-down 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: header-slide-down 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation-fill-mode: initial;
    transition: all 0.3s ease-out 0s;
  }

  &.header-absolute,
  .header-absolute {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: transparent;
  }

  .navbar-toggler-icon {
    background-image: none;
    position: relative;
    background: none;
    text-align: center;
    background: var(--bs-primary);
  }

  .search-box {
    width: 280px;

    .form-group {
      .input-group-text {
        padding: 12px 0 12px 16px;
      }

      .form-control {
        font-size: 14px;
        padding: 12px 16px;
        + .input-group-text {
          padding: 12px 16px 12px 16px;
        }
      }
    }
  }

  .navbar-nav {
    .search-box {
      &.dropdown-menu {
        padding: 0;
        background: transparent;
      }
    }
  }

  // header search
  &.header-search {
    .dropdown {
      .dropdown-toggle-item {
        display: inline-block;
        color: var(--bs-body-color);
        padding: 1.25rem 0;
        line-height: 40px;

        &:hover {
          color: var(--bs-white);
        }
      }

      .dropdown-menu {
        border: none;
        margin-top: -2px !important;
        line-height: inherit;
      }

      &.dropdown-search-wrapper {
        width: 100%;

        .search-box {
          width: 100%;
          padding: 0;
          background-color: transparent;
        }
      }
    }
  }
}

@include media-breakpoint-up(sm) {

  // header search
  header {
    &.header-search {
      .search-box {
        display: block;
        position: static;
      }
    }
  }
}

/*====================
Slide Down Animation
=========================*/
@-webkit-keyframes header-slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes header-slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@include media-breakpoint-up(md) {
  header {
    .navbar-nav {
      .search-box {
        &.dropdown-menu {
          display: inline-block;
          margin: 0;
          padding: 0;
          top: 50%;
          right: 0;
          background: transparent;
          transform: scaleX(0) translateY(-50%);
          transform-origin: 100% 100%;
          transition: all .3s ease-in-out;

          &.show{
            transform: scaleX(1) translateY(-50%);
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(md){
  header {
    .navbar-nav {
      .search-box {
        &.dropdown-menu {
          display: inline-block;
          width: 100%;
          margin: 0;
          padding: 0;
          position: static;
          background: transparent;
          transition: all .3s ease-in-out;
        }
      }
    }
  }
}

@media(max-width: 319.98px) {
  header {
    .search-box {
      width: 100%;
    }
  }
}